<template>
  <div class="practise-edit">
    <head-top :title="'第' + practiseInfo.index + '道习题编辑'">
      <span slot="right" @click="saveData">
        <van-icon name="success" class="head-right"></van-icon>
      </span>
    </head-top>

    <van-cell-group>
      <van-field name="radio" label="题型：">
        <template #input>
          <van-radio-group v-model="practiseType" direction="horizontal" @change="changePractiseType">
            <van-radio v-for="(item, index) in practiseTypeOptions" :key="index" :name="item.value"
                       style="margin-bottom: 6px;">
              {{item.label}}
            </van-radio>
          </van-radio-group>
        </template>
      </van-field>

      <van-field required label="标题：" placeholder="请输入题目标题" v-model="practiseInfo.title" type="textarea"
                 maxlength="1000" :error-message="practiseInfoErr.title" clearable autosize show-word-limit>
      </van-field>

      <van-field v-if="[1, 2].indexOf(practiseInfo.type) > -1" name="stepper" label="选项：">
        <template #input>
          <van-stepper integer min="2" max="8" v-model="optionsNum" @change="optionsChange"/>
        </template>
      </van-field>

      <van-cell-group v-if="[1, 2].indexOf(practiseInfo.type) > -1" style="padding-left: 20px;">
        <van-field required v-for="(item, index) in practiseInfo.options" :key="index" placeholder="请输入选项内容"
                   :label="singleOptions[index] + '、'" v-model="item.label" maxlength="50" clearable
                   :error-message="practiseInfoErr.options[index].label">
        </van-field>
      </van-cell-group>

      <van-field required v-show="practiseType === 1" label="答案：" :error-message="practiseInfoErr.answer">
        <template #input>
          <van-radio-group v-model="practiseInfo.answer" direction="horizontal">
            <van-radio v-for="index in optionsNum" :key="index" :name="singleOptions[index - 1]"
                       style="margin-bottom: 6px;">
              {{singleOptions[index - 1]}}
            </van-radio>
          </van-radio-group>
        </template>
      </van-field>
      <van-field required v-show="practiseType === 2" label="答案：" :error-message="practiseInfoErr.multiAnswer">
        <template #input>
          <van-checkbox-group v-model="practiseInfo.multiAnswer" direction="horizontal">
            <van-checkbox shape="square" v-for="index in optionsNum" :key="index" :name="singleOptions[index - 1]"
                          :value="practiseInfo.multiAnswer.indexOf(singleOptions[index - 1])>-1"
                          style="margin-bottom: 6px;">
              {{singleOptions[index - 1]}}
            </van-checkbox>
          </van-checkbox-group>
        </template>
      </van-field>
      <van-field required v-show="practiseType === 3" label="答案：" :error-message="practiseInfoErr.answer">
        <template #input>
          <van-radio-group v-model="practiseInfo.answer" direction="horizontal">
            <van-radio v-for="item in judgeOptions" :key="item" :name="item">
              {{item}}
            </van-radio>
          </van-radio-group>
        </template>
      </van-field>
      <van-field v-show="practiseType === 4" label="答案：" placeholder="请输入题目答案" v-model="practiseInfo.answer"
                 type="textarea" maxlength="1000" :error-message="practiseInfoErr.answer" clearable autosize
                 show-word-limit>
      </van-field>

      <van-field name="stepper" label="分数：" :error-message="practiseInfoErr.score">
        <template #input>
          <van-stepper integer min="0" max="1000" v-model="practiseInfo.score"/>
        </template>
      </van-field>

      <van-field label="解析：" placeholder="请输入题目解析" v-model="practiseInfo.analysis"
                 type="textarea" maxlength="1000" :error-message="practiseInfoErr.analysis" clearable autosize
                 show-word-limit>
      </van-field>

    </van-cell-group>

  </div>
</template>

<script>
export default {
  name: "practiseEdit",
  data () {
    return {
      optionsNum: 4,
      libraryID: this.$store.getters.practiseLibrary ? this.$store.getters.practiseLibrary.id : "",
      practiseSubject: this.$store.getters.practiseSubject,
      practiseType: 1,
      practiseInfo: {
        id: "",
        index: 1,
        title: "",
        answer: "",
        multiAnswer: [],
        analysis: "",
        type: 1,
        optionsNum: 4,
        score: 0,
        options: []
      },
      practiseInfoErr: {
        title: "",
        answer: "",
        multiAnswer: "",
        // analysis: "",
        // score: "",
        options: []
      },
      practiseTypeOptions: [
        { label: "单选题", value: 1 },
        { label: "多选题", value: 2 },
        { label: "判断题", value: 3 },
        { label: "问答题", value: 4 }
      ],
      singleOptions: ["A", "B", "C", "D", "E", "F", "G", "H"],
      judgeOptions: ["对", "错"]
    };
  },
  mounted () {
    this.practiseInfo.index = this.practiseSubject.index;
    if (this.practiseSubject.id) {
      this.practiseInfo.id = this.practiseSubject.id;
      this.practiseInfo.title = this.practiseSubject.title;
      this.practiseInfo.type = this.practiseSubject.type;
      this.practiseType = this.practiseSubject.type;
      this.practiseInfo.optionsNum = this.practiseSubject.options.length;
      this.practiseInfo.score = this.practiseSubject.score;
      this.practiseInfo.analysis = this.practiseSubject.analysis;
      this.optionsChange(this.optionsNum);
      this.practiseInfo.options = this.practiseSubject.options.slice(0, this.practiseSubject.options.length);
      setTimeout(() => {
        this.practiseInfo.answer = this.practiseSubject.answer;
        let multiAnswer = this.practiseInfo.type === 2 ? this.practiseSubject.answer.split('') : [];
        this.practiseInfo.multiAnswer = multiAnswer.slice(0, multiAnswer.length);
      }, 0);
    } else {
      this.optionsChange(this.optionsNum);
    }
  },
  methods: {
    changePractiseType (value) {
      this.practiseInfo.type = value;
      this.practiseType = value;
      this.practiseInfo.answer = "";
      this.practiseInfo.multiAnswer = [];
    },
    optionsChange (value) {
      if (this.practiseInfo.options.length < value) {
        for (let i = this.practiseInfo.options.length; i < value; i++) {
          this.practiseInfo.options.push({
            id: "",
            value: this.singleOptions[i],
            label: ""
          });
          this.practiseInfoErr.options.push({
            label: ""
          });
        }
      } else if (this.practiseInfo.options.length > value) {
        const optionsDelete = this.practiseInfo.options.filter((k, r) => r >= value);
        this.practiseInfo.options.splice(value, this.practiseInfo.options.length - value);
        this.practiseInfoErr.options.splice(value, this.practiseInfo.options.length - value);
        if (this.practiseInfo.answer) {
          // 移除单选的答案
          if (optionsDelete.find(r => r.value === this.practiseInfo.answer)) {
            this.practiseInfo.answer = "";
          }
        } else if (this.practiseInfo.multiAnswer.length > 0) {
          // 移除多选的答案
          this.practiseInfo.multiAnswer.forEach((item, index) => {
            if (optionsDelete.find(r => r.value === item)) {
              this.practiseInfo.multiAnswer.splice(index, 1);
            }
          });
        }
      }
    },
    saveData () {
      this.practiseInfoErr = {
        title: "",
        answer: "",
        multiAnswer: "",
        // analysis: "",
        // score: "",
        options: this.practiseInfoErr.options.map(r => {
          return { label: "" }
        })
      };
      if (this.practiseInfo.title === "") {
        this.practiseInfoErr.title = "请输入题目标题";
        return;
      }
      if ([1, 3].indexOf(this.practiseInfo.type) > -1 && this.practiseInfo.answer === "") {
        this.practiseInfoErr.answer = "请选择答案";
        return;
      } else if (this.practiseInfo.type === 2 && this.practiseInfo.multiAnswer.length === 0) {
        this.practiseInfoErr.multiAnswer = "请选择答案";
        return;
      }
      if ([1, 2].indexOf(this.practiseInfo.type) > -1) {
        for (let i = 0; i < this.practiseInfo.options.length; i++) {
          if (this.practiseInfo.options[i].label === "") {
            this.practiseInfoErr.options[i].label = "请输入选项内容";
            return;
          }
        }
      }

      let subjectMain = {
        PLID: this.libraryID,
        PSType: this.practiseInfo.type,
        PSContent: this.practiseInfo.title,
        PSAnswer: this.practiseInfo.type === 2 ? this.practiseInfo.multiAnswer.sort().join("") : this.practiseInfo.answer,
        PSanalysis: this.practiseInfo.analysis,
        PSScore: this.practiseInfo.score
      };
      if (this.practiseInfo.id) subjectMain.PSID = this.practiseInfo.id;
      let subjectOption = {
        Datas: [3, 4].indexOf(this.practiseInfo.type) > -1 ? [] : this.practiseInfo.options.map(r => {
          return { SOSerial: r.value, SOContent: r.label };
        })
      };

      let reqData = {
        psdata: JSON.stringify(subjectMain),
        sodata: JSON.stringify(subjectOption)
      };

      if (this.practiseInfo.id) {
        this.$mainHttp.classPractise.SubjectUpdate(reqData).then(res => {
          if (res.Result) {
            this.$toast("保存成功");
            this.$router.replace("/practise-library/edit");
          } else if (res.Info) {
            this.$toast(res.Info);
          } else {
            this.$toast("保存失败");
          }
        }).catch(() => {
          this.$toast("操作失败");
        });
      } else {
        this.$mainHttp.classPractise.SubjectAdd(reqData).then(res => {
          if (res.Result) {
            this.$toast("保存成功");
            this.$router.replace("/practise-library/edit");
          } else if (res.Info) {
            this.$toast(res.Info);
          } else {
            this.$toast("保存失败");
          }
        }).catch(() => {
          this.$toast("操作失败");
        });
      }
    }
  }
}
</script>

<style lang="less" scoped>
@import "./../../../styles/common/base";

.practise-edit {

  /deep/ .van-field__label {
    width: 50px;
  }
}
</style>
